import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	name: "tuiTabbar",
	props: {
		//字体颜色
		color: {
			type: String,
			default: "#666"
		},
		//字体选中颜色
		selectedColor: {
			type: String,
			default: "#5677FC"
		},
		//背景颜色
		backgroundColor: {
			type: String,
			default: "#FFFFFF"
		},
		//是否需要中间凸起按钮
		hump: {
			type: Boolean,
			default: true
		},
		//固定在底部
		isFixed: {
			type: Boolean,
			default: true
		},
		//角标字体颜色
		badgeColor: {
			type: String,
			default: "#fff"
		},
		//角标背景颜色
		badgeBgColor: {
			type: String,
			default: "#F74D54"
		},
		unlined: {
			type: Boolean,
			default: false
		}

	},
	computed: mapState(["tabBarIndex","tabBar"]),
	created() {
		this.current = this.tabBarIndex
	},
	watch: {
		tabBarIndex() {
			this.current = this.tabBarIndex
		}
	},
	data() {
		return {
			current: 0
		};
	},
	methods: {
		...mapMutations(['changeTabBar']),
		tabbarSwitch(index, hump, pagePath, verify) {
			if (verify) {
				this.changeTabBar({
					index: index
				})
				uni.switchTab({
					url: pagePath
				})
			} else {
				this.$emit("click", {
					index: index,
					hump: hump,
					pagePath: pagePath,
					verify: verify
				})
			}

		}
	}
}
